<!--  -->
<template>
    <swiper>
        <swiper-item v-for="(item, index) in banners" :key="index"> 
            <img :src="item.image" alt="" @load="SwiperImageLoad">
        </swiper-item>
    </swiper>
</template>

<script>
import {Swiper,SwiperItem} from 'components/common/swiper'
export default {
    name: 'HomeSwiper',
    components: {Swiper,SwiperItem},
    props:{
        banners: {
            type: Array,
            default() {
                return []
            }
        }
    },
    data() {
        return {
            isLoad: false
        };
    },
    //方法集合
    methods: {
        SwiperImageLoad(){
            if (!this.isLoad) {
                this.$emit('swiperImageLoad')
                this.isLoad = true
            }
        }
    },
}
</script>
<style>
img{
    position: relative;
}
</style>