<!--  -->
<template>
    <div id="home">
        <nav-bar class="home_nav">
            <div slot="center">首页</div>
        </nav-bar>
        <tab-control 
            class="tab_control" 
            :tabList="tabList" 
            @handleTabClick="handleTabClick($event)" 
            ref="tabControl1" 
            v-show="isTabFixed"/>
        <Scroll class="scroll_content" 
                ref="scroll" 
                :probe-type="3" 
                @backTopScroll="positionScroll" 
                :pull-up-load="true"
                @pullingUp="pullingUp">
            <home-swiper 
                @swiperImageLoad="swiperImageLoad"
                :banners="banners"/>
            <home-recommend-view :recommends="recommends"/>
            <tab-control 
                :tabList="tabList" 
                @handleTabClick="handleTabClick($event)"
                ref="tabControl2"/>
            <good-list :goods="goods[currentType].list"/>
        </Scroll>
        <back-top @click.native="backClick" v-show="isBackTop"/>   
    </div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'

import Scroll from 'components/common/scroll/Scroll'

import HomeSwiper from './childComps/HomeSwiper'
import HomeRecommendView from './childComps/HomeRecommendView'

import TabControl from 'components/content/tabControl/TabControl'
import GoodList from 'components/content/goodList/GoodList'
import BackTop from 'components/content/backTop/BackTop'

import { getHomeMultiData , getHomeGoods } from 'network/home'
import { itemListenerMixin } from 'common/mixin'

    export default {
        name: 'Home',
        mixins: [itemListenerMixin],
        components: {
            NavBar,
            Scroll,
            HomeSwiper,
            HomeRecommendView,
            TabControl,
            GoodList,
            BackTop
        },
        data() {
            return {
                banners: null,
                recommends: null,
                tabList:[
                    {text:'流行'},
                    {text:'新款'},
                    {text:'精选'}
                ],
                goods: {
                    'p':{
                        page: 0,
                        list: []
                    },
                    'n': {
                        page: 0,
                        list: []
                    },
                    's': {
                        page: 0,
                        list: []
                    },
                },
                currentType: 'p',
                isBackTop: false,
                tabOffsetTop: 0,
                isTabFixed: false,
                saveY: 0
            }
        },
        created() {
            // 1.请求多个数据
            this.getHomeMultiData()

            // 2.请求商品信息
            this.getHomeGoods('p');
            this.getHomeGoods('n');
            this.getHomeGoods('s');


        },
        mounted() {
        },
        activated() {
            this.$refs.scroll.scrollTo(0,this.saveY,0)
            this.$refs.scroll.refresh()
        },
        deactivated() {
            // 保存滚动的Y值
            this.saveY =  this.$refs.scroll.getScrollY()
            // 取消全局变量的监听
            this.$bus.$off('imageLoad', this.itemImageLoad)
        },
        methods: {
            // 网络请求相关方法
            getHomeMultiData(){
                getHomeMultiData().then(res => {
                    this.banners = res.data.banner.list;
                    this.recommends = res.data.recommend.list;
                })
            },
            getHomeGoods(query) {
                // 获取并合并新列表
                const pagenum = this.goods[query].page + 1
                getHomeGoods(query, pagenum).then(res => {
                    this.goods[query].list.push(...res.message.goods);
                    this.goods[query].page += 1
                    this.$refs.scroll.finishPullUp()
                }) 
            },
            // 监听tab的点击
            handleTabClick(index){
                switch (index) {
                    case 0:
                        this.currentType = 'p'        
                        break;
                    case 1:
                        this.currentType = 'n'
                        break;
                    case 2:
                        this.currentType = 's'
                        break;
                }
                this.$refs.tabControl1.currentIndex = index + 1
                this.$refs.tabControl2.currentIndex = index + 1
            },
            // 回到顶部
            backClick(){
                // 获取scroll组件
                this.$refs.scroll.scrollTo(0,0,500)
            },
            // 监听scroll位置
            positionScroll(position){
                this.isBackTop = (-position.y) >= 1000
                this.isTabFixed = (-position.y) >= this.tabOffsetTop
                this.saveY = position.y
                
            },
            //  监听scroll滚动到底部
            pullingUp(){
                this.getHomeGoods(this.currentType);
            },
            // 监听轮播图图片是否加载
            swiperImageLoad(){
                this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
            }
        },
    }
</script>
<style scoped>
#home{
    box-sizing: border-box;
    /* margin-top: 44px; */
    position: relative;
    height: 100vh;
}
.home_nav{
    background-color: var(--color-tint);
    color: #fff;

    /* position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99; */
}
.tab_control{
    position: relative;
    z-index: 99;
    background-color: #fff;
}
.scroll_content{
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 62px;
    left: 0;
    right: 0;
}

</style>